'use client'

import { useEffect, useState } from 'react'
import { Card } from "@/components/ui/card"
import { Progress } from "@/components/ui/progress"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"

interface TaskProgress {
  category: string
  completed: number
  total: number
}

interface AlertItem {
  id: number
  title: string
  description: string
  type: 'default' | 'destructive'
  timestamp: string
}

export default function ManagerDashboard() {
  const [progress, setProgress] = useState<TaskProgress[]>([
    { category: 'Daily Checklists', completed: 7, total: 10 },
    { category: 'Temperature Logs', completed: 12, total: 15 },
    { category: 'Test Records', completed: 4, total: 5 }
  ])

  const [alerts, setAlerts] = useState<AlertItem[]>([
    {
      id: 1,
      title: 'Temperature Alert',
      description: 'Freezer #2 temperature above threshold',
      type: 'destructive',
      timestamp: '10 minutes ago'
    },
    {
      id: 2,
      title: 'Checklist Due',
      description: 'Evening cleaning checklist needs completion',
      type: 'default',
      timestamp: '1 hour ago'
    },
    {
      id: 3,
      title: 'Water Test Required',
      description: 'Schedule daily water quality test',
      type: 'default',
      timestamp: '2 hours ago'
    }
  ])

  return (
    <div className="min-h-screen bg-gray-50 p-8">
      <div className="max-w-7xl mx-auto space-y-8">
        <div className="flex justify-between items-center">
          <h1 className="text-3xl font-bold">Manager Dashboard</h1>
          <p className="text-gray-600">Welcome back, Manager</p>
        </div>

        <Tabs defaultValue="progress" className="space-y-6">
          <TabsList>
            <TabsTrigger value="progress">Progress</TabsTrigger>
            <TabsTrigger value="alerts">Alerts</TabsTrigger>
          </TabsList>

          <TabsContent value="progress">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {progress.map((item) => (
                <Card key={item.category} className="p-6 space-y-4">
                  <h3 className="font-semibold text-lg">{item.category}</h3>
                  <Progress 
                    value={(item.completed / item.total) * 100} 
                    className="h-2"
                  />
                  <div className="flex justify-between text-sm text-gray-600">
                    <span>{item.completed} completed</span>
                    <span>{item.total} total</span>
                  </div>
                </Card>
              ))}
            </div>
          </TabsContent>

          <TabsContent value="alerts">
            <div className="space-y-4">
              {alerts.map((alert) => (
                <Alert key={alert.id} variant={alert.type}>
                  <div className="flex justify-between items-start">
                    <div>
                      <AlertTitle>{alert.title}</AlertTitle>
                      <AlertDescription>{alert.description}</AlertDescription>
                    </div>
                    <span className="text-sm text-gray-500">{alert.timestamp}</span>
                  </div>
                </Alert>
              ))}
            </div>
          </TabsContent>
        </Tabs>

        {/* Quick Actions */}
        <section className="space-y-4">
          <h2 className="text-xl font-semibold">Quick Actions</h2>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <Card className="p-4 hover:bg-gray-50 cursor-pointer transition-colors">
              <h3 className="font-medium">New Temperature Log</h3>
              <p className="text-sm text-gray-600">Record temperature readings</p>
            </Card>
            <Card className="p-4 hover:bg-gray-50 cursor-pointer transition-colors">
              <h3 className="font-medium">Start Checklist</h3>
              <p className="text-sm text-gray-600">Begin a new checklist</p>
            </Card>
            <Card className="p-4 hover:bg-gray-50 cursor-pointer transition-colors">
              <h3 className="font-medium">View Reports</h3>
              <p className="text-sm text-gray-600">Access recent reports</p>
            </Card>
            <Card className="p-4 hover:bg-gray-50 cursor-pointer transition-colors">
              <h3 className="font-medium">Test Records</h3>
              <p className="text-sm text-gray-600">Manage test results</p>
            </Card>
          </div>
        </section>
      </div>
    </div>
  )
}
